<script>
    import Chart from './Chart.svelte';
    import Checkbox from './Checkbox.svelte';
    import Select from '../svelte-select/src/Select.svelte';
    import data from './data.js';
    import vars from './vars.js';

    function calculate_result(xstart, dx, nstep, var1, var2, var1_l, var2_l) {
        var results = [];
        var xlist = [];

        var i;
        for (i = 0; i < nstep; i++) {
            var xi = xstart + dx * i;
            xlist.push(xi)
            results.push([var1 * xi, var2 * xi, (var1_l + var2_l) * xi]);
        }
        return {
            "xl": xlist,
            "rs": results
        }
    }

    var sum = function (arr, bools) {
        var x = 0;
        for (var i = 0; i < arr.length; i++) {
            x = x + arr[i] * (bools[i] ? 1 : 0)
        }
        return x
    };

    function max(rs, checked) {
        return rs.reduce((max, b) => Math.max(max, sum(b, checked)), sum(rs[0], checked))
    }

    var colors = ["#eb4034", "#65eb34", "#34ebe2", "#f0027f", "#fdc086"];
    const legendheight = 15;

    let state_options = data.state_options;
    let counties = data.counties;

    const default_state = {value: 'DEFAULT', label: '--State--', group: 'State'};
    const default_county = {value: 'DEFAULT', label: '--County--', group: 'County'};

    $: selectedState = default_state;
    $: selectedCounty = default_county;
    $: county_options = selectedState ? counties[selectedState.value] : '';
    $: county_code = selectedCounty ? selectedCounty.value : undefined;

    let zip_code = undefined;
    let zip_code_dict = data.zipcodes;
    $: county_code = zip_code_dict[zip_code] ? zip_code_dict[zip_code]['value'] : undefined

    $: xstart = 0.0;
    let vars_dict = vars.vars;
    $: var1_ = 5.0;
    $: var2_ = 6.0;
    $: dx_ = 0.1;
    $: nstep_ = 100;
    $: vars_list = vars_dict[county_code] ? vars_dict[county_code] : [var1_, var2_, dx_, nstep_];
    $: var1 = vars_list[0];
    $: var2 = vars_list[1];
    $: dx = vars_list[2];
    $: nstep = vars_list[3];
    $: var1 = var1_;
    $: var2 = var2_;
    $: dx = dx_;
    $: nstep =  nstep_;
    $: var1_l = Math.log(var1);
    $: var2_l = Math.log(var2);



    $: rr = calculate_result(xstart, dx, nstep, var1, var2, var1_l, var2_l);
    $: xl = rr["xl"];
    $: rs = rr["rs"];

    $: checked = [true, true, true];
    $: rmax = max(rs, checked);
    $: log_flag = false;
    $: active = 0;
    $: active_ = active >= 0 ? active : nbars - 1;
    $: xstart_min = log_flag ? 0 : -10;
    $: nbars = xl.length;


</script>

<style>
    .select_cs {
        --border: 1px solid grey;
        --borderRadius: 1px;
        --placeholderColor: grey;
        --height: 25px;
    }

    .input_box {
        border: 1px solid black;
        border-radius: 15px;
        background-color: lightblue;
        max-width: 600px;
        height: 250px;
    }
</style>

<h2>PandEcon Test Page</h2>

<b>(a) Select a U.S. State/County</b><br/>
<i>The figure will update itself with randomly generated dummy variables.</i>

<div style="position: relative; top: 20px; left: 10px" class="input_box">
    <div style="position: relative; top: 20px; left: 10px; max-width: 250px; " class="select_cs">
        <div style="position: relative">
            State:
            <Select
                    items={state_options}
                    placeholder="--State--"
                    bind:selectedValue={selectedState}
                    on:select={() => selectedCounty = undefined}
                    on:select={() => zip_code = ''}
            >
            </Select>
            County:
            <Select
                    items={county_options}
                    placeholder="--County--"
                    bind:selectedValue={selectedCounty}
                    on:select={() => zip_code = ''}
            >
            </Select>
        </div>
    </div>

    <div style="position: relative; top:-55px; left: 300px">
        <h4>==OR==</h4>
    </div>

    <div style="position: relative; top: -100px; left: 380px">
        ZIP code: <br/>
        <input bind:value={zip_code}
               placeholder="5-digit ZIP code"
               on:focus={() => selectedState = default_state}
               on:focus={() => selectedCounty = default_county}
        >
    </div>

    <div style="position: relative; top: -85px; left: 380px">
        {#if zip_code_dict[zip_code]}
            {zip_code_dict[zip_code]['county']}, {zip_code_dict[zip_code]['state']}
        {/if}
    </div>

    <div style="position: relative; top: -50px; left: 20px">
        var1: {var1}, var2: {var2}, dx: {dx}, n_step: {nstep}
    </div>
</div>

<br />
<br />
<b>(b) Or you can tune the parameters by yourself.</b>

<!--Input Parameters-->
<div style="position: relative; top: 20px; left: 10px" class="input_box">
    <div style="position: relative; top: 20px; left: 10px">
        Variables <br/>
        <label>
            var1:
            <input type=number step=0.1 bind:value={var1_} min=0 max=10
                   on:focus={() => zip_code = ''}
                   on:focus={() => selectedState = default_state}
                           on:focus={() => selectedCounty = default_county}
            >
            <input type=range step=0.1 bind:value={var1_} min=0 max=10>
            log(var1) = {var1_l.toFixed(2)}
        </label>
        <br/>
        <label>
            var2:
            <input type=number step=0.1 bind:value={var2_} min=0 max=10>
            <input type=range step=0.1 bind:value={var2_} min=0 max=10>
            log(var2) = {var2_l.toFixed(2)}
        </label>
        <br/>
        <label>
            x0:
            <input type=number step=0.1 bind:value={xstart} min={xstart_min} max=10>
            <input type=range step=0.1 bind:value={xstart} min={xstart_min} max=10>
        </label>
        <br/>
        <label>
            dx:
            <input type=number step=0.01 bind:value={dx_} min=0.01 max=10>
            <input type=range step=0.01 bind:value={dx_} min=0.01 max=10>
        </label>
        <br/>
        <label>
            n_step:
            <input type=number step=1 bind:value={nstep_} min=1 max=200>
            <input type=range step=1 bind:value={nstep_} min=1 max=200>
        </label>
        <br/>
        <label>
            <input type=checkbox bind:checked={log_flag}>
            log scale
        </label>
        <br/>
    </div>
</div>


<!--Checkboxes-->
<div style="position:absolute; left:800px; top: 800px" class="check_box">
    <b>(c) Use color-coded checkboxes<br />to select which data array you<br />want to display.</b>
    <div style="position:relative; top:{legendheight*1}px; width: 250px; height: 100px; color: {colors[0]}">
        <Checkbox color="{colors[0]}" bind:checked={checked[0]}/>
        <div class="legend" style="position:relative; left: 20px; top: 4px">
            <div class="legendtitle">Data array 1 &nbsp; value: {rs[active_][0].toFixed(2)}</div>
        </div>
    </div>
    <div style="position:relative; top:{legendheight*1}px; width: 250px; height: 100px; color: {colors[1]}">
        <Checkbox color="{colors[1]}" bind:checked={checked[1]}/>
        <div class="legend" style="position:relative; left: 20px; top: 4px">
            <div class="legendtitle">Data array 2 &nbsp; value: {rs[active_][1].toFixed(2)}</div>
        </div>
    </div>
    <div style="position:relative; top:{legendheight*1}px; width: 250px; height: 100px; color: {colors[2]}">
        <Checkbox color="{colors[2]}" bind:checked={checked[2]}/>
        <div class="legend" style="position:relative; left: 20px; top: 4px">
            <div class="legendtitle">Data array 3 &nbsp; value: {rs[active_][2].toFixed(2)}</div>
        </div>
    </div>
</div>


<!--Chart-->
<br />
<br />
<b>(d) Number in part (c) updates with mouse movement.</b>
<div class="chart" style="display: flex; max-width: 1120px">
    <div style="position:relative; top:60px; left: 10px">
        <Chart bind:checked={checked}
               bind:active={active}
               xl={xl}
               rs={rs}
               log_flag={log_flag}
               rmax={rmax}
               colors={colors}
        />
    </div>
</div>



